body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header__button {
	width: 100%;
}
.header__button, .header__search, .header__limit {
	margin: 20px 0;
}

.button {
	margin-left: 10px;
	padding: 5px 20px;
	display: block;
	width: 100%;
}

.button-wrapper {
	width: 100%;
	display: -webkit-flex;
	display: flex;
}

.user-row__cell_id {
	width: 10%;
}

.user-row__cell_name {
	width: 20%;
}

.user-row__cell_email {
	width: 20%;
}

.user-row__cell_description {
	width: 30%;
}

.user-row__cell_actions{
	width: 20%;
}



.table-header-cell {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}
.table-header-cell__arrows {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: auto 0;
}
.table-header-cell__arrow {
	color: #ccc;
	line-height: 0.3;
	cursor: pointer;
}
.table-header-cell__arrow_active {
	color: #000
}

.footer__button{ 
	width: 100%;
	margin: 5px 0;
}
.footer__info {
	background-color: #F2F2F2;
	color: #333;
	font-size: 16px;
	font-weight: normal;
}
